/* Estilos específicos para el componente de información */
.containerInfo {
    max-width: 600px;
    margin: 20px auto;
    padding: 20px;
    background-color: #f0f4f8;
    border-radius: 10px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    color: #000000;
  }
  
  h2 {
    font-family: 'Lora', serif;
    font-size: 2em;
    color: #009688;
    margin-bottom: 10px;
    text-align: left;
  }
  
  p {
    font-family: 'Roboto', sans-serif;
    font-size: 1.1em;
    line-height: 1.6;
    color: #000000;
    text-align: left;
  }
  
  ul {
    list-style-type: disc; /* Utiliza viñetas (puntos) */
    margin-left: 20px; /* Desplaza la lista a la derecha */
    text-align: left;
  }
  
  li {
    font-family: 'Roboto', sans-serif;
    font-size: 1.1em;
    line-height: 1.6;
    margin-bottom: 10px;
  }
  
  strong {
    color: #009688;
  }
  